  src: url("#{$lato-dir}/Lato-Reg-webfont.eot") format('embedded-opentype'), url("#{$lato-dir}/Lato-Reg-webfont.woff") format("woff"), url("#{$lato-dir}/Lato-Reg-webfont.ttf") format("truetype"); }

