  src: url("#{$lato-dir}/Lato-RegIta-webfont.eot") format('embedded-opentype'), url("#{$lato-dir}/Lato-RegIta-webfont.woff") format("woff"), url("#{$lato-dir}/Lato-RegIta-webfont.ttf") format("truetype"); }

// font-weight: bold (700)
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: bold;
  src: url("#{$lato-dir}/Lato-Bol-webfont.eot") format('embedded-opentype'), url("#{$lato-dir}/Lato-Bol-webfont.woff") format("woff"), url("#{$lato-dir}/Lato-Bol-webfont.ttf") format("truetype"); }

@font-face {
